html{
  min-height:100%;
}

body{
  background-image:url('../site-images/background.png');
  background-repeat:no-repeat;
  background-size:100% 100%;
  background-position:center;
}

.uploadform{
  display:inline-block;
  width:60%;
  margin-left:200px;
}

.headertext{
  font-size:30px;
  position: relative;
}


form{
  background-color:#b0b0b0 !important;
}

.dropzone {
  border: 2px dashed #EB7260;
  padding: 20px;
  text-align: center;
}

.dropzone h3 {
  text-align: center;
  line-height: 3em;
  margin-top: 20px;
}

.dz-clickable {
  cursor: pointer;
}

.dz-drag-hover {
  border: 2px solid #EB7260;
}

.dz-preview, .dz-processing, .dz-image-preview, .dz-success, .dz-complete {
  padding: 5px;
}

.dz-preview {
  width: auto !important;
}

.dz-image {
  text-align: center;

}

.dz-progress{
  margin-top:35px !important;
}

.dz-details {
  padding: 10px;
}

.dz-success-mark{
  display: none;
  background-color:green;
  border-radius: 25px;
}
.dz-error-mark p{
  margin-right:35px;
  margin-top:19px;
}